header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
    background: transparent;
    transition: all ease .7s;
}
header.black{
background: #141414;
}
.header--logo{
    height: 30px;
}
.header--logo img{
    height: 100%;
}
.header--user{
    height: 40px;
}
.header--user img{
    height: 100%;
    border-radius: 5px;
}